<script>
import tabbar from "./components/common/tabbar";
import { mapState, mapGetters } from "vuex";
import { Dialog } from "vant";
export default {
  name: "App",
  components: {
    tabbar,
  },
  created() {
    this.isLogin && this.$store.commit("getUserInfo");
    // !this.$storage("locale") && this.$storage("locale", "cn");
    // this.$store.commit("setLang", this.$storage("locale"));
  },
  data() {
    return {
      show: true,
      drawlShow: true,
      version_code: 110,
      link: "",
      make: "",
    };
  },
  mounted() {
    window.readDeviceId = this.readDeviceId;
    window.getClientId = this.getClientId;
    this.fullscreenJs();
    this.getCode();
  },
  methods: {
    onClose() {
      this.show = false;
    },
    Close() {
      this.drawlShow = false;
    },
    readDeviceId(params) {
      console.log(params);
      localStorage.setItem("readDeviceId", params);
      return;
    },
    getCode() {
      let that = this;
      const u = navigator.userAgent;
      const isAndroid = u.indexOf("Android") > -1 || u.indexOf("Linux") > -1;
      const isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      let client_type = "";
      if (isAndroid) {
        client_type = "android";
      } else if (isIOS) {
        client_type = "ios";
      }
      this.$axios
        .post("/user/checkVersion", {
          client_type: client_type,
        })
        .then((res) => {
          this.link = "https://app.hpl.plus/";
          //   this.make = res.data.mark;
          console.log(res.data);
          // console.log(this.version_code);
          if (this.version_code < res.data) {
            // this.drawlShow = true;
            console.log("");
            // console.log(that.version_code);
            Dialog.confirm({
              title: "",
              showCancelButton: false,
            }).then((e) => {
              that.scannClick();
              window.location.href = that.link;
              that.version_code = res.data;
            });
          }
        });
    },
    getClientId(params) {
      localStorage.setItem("getClientId", params);
      return params;
    },
    //
    fullscreenJs() {
      (function () {
        var sdk = window.SDK || {};
        sdk.openUrl = function (str) {
          window.top.postMessage({ action: "openUrl" }, "*");
        };
        window.SDK = sdk;
      })();
    },
    scannClick() {
      var that = this;
      try {
        android.linkUrl(that.link); // cameraopenUrl
      } catch (e) {
        try {
          window.webkit.messageHandlers.openUrl.postMessage(that.link);
        } catch (e) {
          window.SDK.openUrl(that.link); //iOS
        }
      }
    },
  },
  computed: {
    ...mapGetters(["isLogin"]),
  },
  watch: {},
};
</script>

