<template>
  <div class="t-white bg-view py-15 px-20">
    <!--  -->
    <p class="fs-lg t-center"></p>
    <van-field
      placeholder=""
      type="password"
      :border="false"
      v-model="pwdValue"
      class="inputpwd bg-black1 m-t-30"
    ></van-field>
    <div class="d-flex jc-between ai-center fs-lg my-20">
      <van-button class="btn bg-green t-white" @click="onCancel"
        ></van-button
      >
      <van-button class="btn bg-green t-white" @click="onConfirm"
        ></van-button
      >
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pwdValue: null,
    };
  },
  methods: {
    onCancel() {
      this.pwdValue = "";
      this.$emit("hidden", false);
    },
    onConfirm() {
      if (!this.pwdValue) {
        this.$toast("");
        return;
      }
      this.$toast.loading({
        duration: 0, //  toast
        forbidClick: true, // 
        loadingType: "spinner",
        message: "loading...",
      });
      this.$axios
        .post("/user/check/payPassword", {
          payPassword: this.pwdValue,
        })
        .then((res) => {
          // console.log(res);
          this.$emit("pwdValue", this.pwdValue);
          this.$emit("onConfirm", false);
        });
      this.pwdValue = "";
    },
  },
};
</script>

<style lang="scss" scoped>
.inputpwd {
  height: 88px;
}
.btn {
  width: 48%;
  height: 88px;
  &:nth-child(1) {
    opacity: 0.5;
  }
}
</style>