const captcha = {
    state: {
        keys: 'captcha',
        captchaRefresh: 1, //
        validate: "", //

        isWeb3: false, //web3
        isMm: true, //metamask
        isNetworkId: null,//id
        isVersion: '', //web3
        isAddress: '',//
        isBalance: '',//
        isTokens: [],//
        isToken: '',

        issol: '',//
        accounts: '', //
        toPath: '', //
    },
    mutations: {
        accounts(state, e){
            state.accounts = e;
        },
        // 
        getPath(state, e){
            state.toPath = e;
        },
        //token
        setToken(state, e) {
            state.isToken = e;
        },
        //
        getIssol(state, e) {
            state.issol = e;
        },
        //
        setIsTokens(state, e) {
            state.isTokens = e;
        },
        //
        setIsBalance(state, e) {
            state.isBalance = e;
        },
        //
        setIsAddress(state, e) {
            state.isAddress = e;
        },
        //id
        setIsNetworkId(state, e) {
            state.isNetworkId = e;
            state.isAddress = '';
        },
        //web3
        setIsWeb3(state, e) {
            state.isWeb3 = e;
        },
        //metamsk
        setIsMm(state, e) {
            state.isMm = e;
        },
        //web3
        setIsVersion(state, e) {
            state.isVersion = e;
        },
        setValidate(state, params) {
            state.validate = params;
        },
        captchaRefreshFun(state) {
            //
            state.captchaRefresh++;
        },
    },
    actions: {
    },
    modules: {
    }
}
export default captcha;